import blog from '@/api/blog';
export default{
    data(){
        return {
            title:'',
            content:'',
            description:'',
            atIndex:'',
            // 字数限制
            titleLength:0,
            contentLength:0,
            descriptionLength:0
        }
    },
    methods:{
        //直接调用 api不用vuex,因为没有共享的数据
        onCreate(){
            blog.createBlog({title:this.title,
                content:this.content,description:this.description,
                atIndex:this.atIndex}).then(res=>{
                    this.$message.success(res.msg)
                    this.$router.push({path:`/detail/${res.data.id}`})
                })
        },
        //TODO
        checkTitleLength(){

        }
    }
}