import blog from '@/api/blog'
export default {
    data() {
        return {
            blogs: [],
            total: 0,
            page: 1
        }
    },
    created() {
        //获取地址栏中的页数
        // this.page = parseInt(this.$router.query.page);
        this.page = 2;
        //传递页码参数
        blog.getIndexBlogs({page:this.page}).then(res => {
            this.blogs = res.data,
                this.total = res.total,
                this.page = res.page
        })
    },
    methods: {
        onPageChange(newPage) {
            //看下这个参数是什么
            // console.log(argument);
            blog.getIndexBlogs({ page: newPage }).then(res => {
                this.blogs = res.data,
                this.total = res.total,
                this.page = res.page
                //点下一页将跳转到首页带上相应的页码，这样进来时获取数据就会带上页码，
                //从而获得要去的页码数据
                // this.$router.push({path:`/?page=${newPage}`})
                this.$router.push({path:'/',query:{page:newPage}})
            })
        }
    }

}