<template>
  <header :class="{ login: isLogin}">
    <template v-if="!isLogin">
      <h1><router-link to="/">写博客啦</router-link></h1>
      <p>博客集合</p>
      <div class="btns">
         <router-link to="/login"><el-button>立即登录</el-button></router-link>
         <router-link to="/register">  <el-button> 注册账号</el-button></router-link>
      </div>
    </template>
    <template v-if="isLogin">
      <h1><router-link to="/">写博客啦</router-link></h1>
    <router-link to="/create">  <i class="edit el-icon-edit"></i></router-link>
      <!-- <img class="avatar" src="http://cn.gravatar.com/avatar/1?s=128&d=identicon" /> -->
      <div class="user">
         <img class="avatar" :src="user.avatar" />
         <ul>
           <li><router-link to="my">我的</router-link></li>
           <li @click = "onLogout">注销</li>
         </ul>
      </div>
    </template>
  </header>
</template>

<script>
//将store 中 state 和 actions 映射到 methods中，可以直接调用
import { mapState,mapActions, mapGetters } from 'vuex';
export default {
  data() {
    //存放自己页面需要的数据
    return {
        //或者直接拿
         name:''
    }
  },
  computed:{
    //拿出 store 中的 getter
    ...mapGetters(['isLogin','user']),
    // test(){
    // //  console.log(this.$store.state.test)// 根state
    // //  console.log(this.$store.state.auth.isLogin) module中的状态是挂载这里的
    //  return 'abc'
    // }
  },

  created(){
    //检验用户是否登录
    this.checkLogin();
  },
  methods:{
    //映射
    ...mapActions([
      //相当于 checkLogin 就成为了这个组件中的方法，可以直接使用
      //调用这个函数相当于执行  this.$store.dispatch('函数','参数')
      'checkLogin',
      'logout'
    ]),
    onLogout(){
      console.log('log-----')
      this.logout();
    }

  }
};
</script>

<style lang="less">
@import "~@/assets/styles/base.less";
header {
