    width:80px;
    height:80px;
    border-radius:50%;
  }
}

h1 {
  color: #fff;
  font-size: 40px;
  margin: 60px 0 0 0;
  text-transform: uppercase;
}


.btns {
  margin-top: 20px;
  text-align: center;
  button{
    margin-left:10px;
  }
 
}
// 用户头像下拉列表
.user{
